  font-family: Roboto, sans-serif;
  font-size: 14px;
}

body.light-theme, body.light-theme a {
  color: var(--header-bg-color);
  background-color: var(--main-light-color);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
